.talk-in-code-app-form{
	margin-top: 30px;
	overflow: hidden;
	
	.cta-wrapper{
		margin-top: 30px;
		position: relative;
		
		@media(min-width: $sm){
			padding: 0px 20px;
		}
		
		@media(min-width: $md){
			padding: 0px 30px;
		}
	}
	
	.share-links-wrapper{
		&__link{
			margin-left: 10px;

			@media(min-width: $md){
				margin-left: 25px;
			}

			&:first-child{
				margin-left: 0px;
			}
		}
	}
}