@import "fonts";

/*-------------Color codes-------------*/

$black: #4b4b4b;
$gray: #5c5c5c;
$light-gray: #8b8b8b;
$lighter-gray: #d2d2d2;
$yellow: #f6c500;

/*-------------END: Color codes-------------*/

/*------------Screen variables------------*/

$sm: 768px;
$md: 1200px;

/*------------END: Screen variables------------*/

/*-------------Base styles-------------*/

body{
	min-height: 100vh;
    height: auto;
}

*{
	outline: none;
}

a{
	display: inline-block;
	text-decoration: none;
}

button{
	background: none;
	border: 0;
	padding: 0;
}

textarea{
	resize: none;
	border: 0;
	padding: 0;
	background: none;
}

body{
	background: url("../img/background-img.jpg");
	background-size: cover;
    background-repeat: no-repeat;
}

/*-------------END: Base styles-------------*/

/*---------------Text mixins---------------*/

@mixin font-styles($xs-f, $sm-f, $md-f, $case, $font-family, $color: $black){
	color: $color;
	text-transform: $case;
	font-size: $xs-f;
	font-family: $font-family;
	
	@media(min-width: $sm){
		font-size: $sm-f;
	}
	
	@media(min-width: $md){
		font-size: $md-f;
	}
}

.big-text{
	@include font-styles(40px, 60px, 82px, uppercase, $bebus);
	
	&--gray{
		color: $gray;
	}
}

.sm-label-text{
	@include font-styles(12px, 14px, 17px, none, $r-bold);
}

/*---------------END: Text mixins---------------*/