.header-wrapper{
	background: white;
    padding: 12px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
	box-shadow: 0 2px 10px -2px rgba(0,0,0,0.1);

	.logo-block{
		&__img{
			height: 10px;
			
			@media(min-width: $sm){
				height: 17px;
			}
		}
	}
}