.talk-in-code-app-heading{
	
	@media(min-width: $sm){
		display: flex;
    	align-items: center;
		padding: 0 20px;
	}
	
	&__logo{
		height: 20px;
		
		@media(min-width: $sm){
			margin-left: 25px;
			height: 25px;
		}
		
		@media(min-width: $md){
			margin-left: 35px;
			height: 30px;
		}
	}
}

.talk-in-code-app{
	&__brief-info{
		margin-top: 20px;
		
		@media(min-width: $sm){
			padding: 0 20px;
		}
		
		@media(min-width: $md){
			padding: 0 30px;
		}
	}
}