.base-btn{
	@include font-styles(15px, 26px, 30px, uppercase, $bebus, $yellow);
	background: $gray;
    padding: 15px;
	cursor: pointer;
	transition: 0.2s ease;
	
	&:hover{
		background: $yellow;
		color: $gray;
	}
	
	@media(min-width: $sm){
		padding: 20px 30px;
	}
	
	@media(min-width: $md){
		padding: 30px 40px;
	}
}