/*--------------Firefox browser version 3.6+--------------*/

@media screen and (-moz-images-in-menus:0) {
	.label-ele{
		transform: none;
		top: 0%;

		&.is-hidden{
			top: -50%;
		}
	}
	
	.convert-text{
		transform: none;
		margin-left: 0%;

		&.is-hidden{
			margin-left: -100%;
		}
	}

	.shares-btns{
		transform: none;
		margin-left: -100%;
		
		&.is-visible{
			margin-left: 0%;
		}
	}
}

/*--------------END: Firefox browser version 3.6+--------------*/

/*-----------------------IE9-----------------------*/

@media screen and (min-width:0\0) {
	.header-wrapper{
		overflow: auto;
		
		.share-display-block{
			float: left;
		}
		
		.logo-block{
			float: right;
		}
	}
}

/*-----------------------END: IE9-----------------------*/

/*-----------------------IE8-----------------------*/

@media \0screen\,screen\9 {
	.label-ele{
		transform: none;
		top: 0%;

		&.is-hidden{
			top: -50%;
		}
	}
	
	.convert-text{
		transform: none;
		margin-left: 0%;

		&.is-hidden{
			margin-left: -100%;
		}
	}

	.shares-btns{
		transform: none;
		margin-left: -100%;
		

		&.is-visible{
			margin-left: 0%;
		}
	}
	
	//Base styles
	.big-text{
		font-size: 82px;
	}

	.sm-label-text{
		font-size: 17px;
	}
	//END: Base styles
	
	//Component: button
	
	.base-btn{
		font-size: 30px;
		padding: 30px 40px;
	}
	
	//END: Component: button
	
	//Component: Textarea
	
	.field-box{
		width: 760px;
		max-width: inherit;
		padding: 0 30px;
		
		.textarea-wrapper{
			width: 90%;

			.reset-field{

				&__img{
					height: auto;
				}
			}
		}

		.textarea{
			height: 325px;
		}
	}
	
	//END: Component: Textarea
	
	//Container
	.container{
		@include center($pad: 190px);
		padding-top: 100px;
	}
	//END: Container
	
	//Page header
	.header-wrapper{
		overflow: auto;
		border-bottom: 1px solid $lighter-gray;
		
		.logo-block{
			float: right;
			
			&__img{
				height: 17px;
			}
		}
	}
	//END: Page header
	
	//For form layout
	.talk-in-code-app-form{

		.cta-wrapper{
			padding: 0px 30px;
		}

		.share-links-wrapper{
			&__link{
				margin-left: 25px;

				&:first-child{
					margin-left: 0px;
				}
			}
		}
	}
	//END: For form layout
	
	//App Heading
	.talk-in-code-app-heading{
		padding: 0 20px;
		overflow: hidden;
		
		&__head{
			float: left;
		}

		&__logo{
			margin-left: 35px;
			margin-top: 35px;
			height: 30px;
			float: left;
		}
	}
	
	.talk-in-code-app{
		&__brief-info{
			padding: 0 30px;
		}
	}
	//END: App Heading
}

/*-----------------------END: IE8-----------------------*/