/*--------------------Label Animation--------------------*/
.label-ele{
	transform: scaleY(1) translateY(0);
	transform-origin: top;
	opacity: 1;
	transition: all 0.1s ease;
	
	&.is-hidden{
		transform: scaleY(0) translateY(-100%);
		opacity: 0;
	}
}
/*--------------------END: Label Animation--------------------*/

/*--------------------Button animation--------------------*/
.convert-text{
	transform: scaleX(1);
	transform-origin: right;
	transition: 0.2s ease;

	&.is-disabled{
		pointer-events: none;
		background: $lighter-gray;
		color: $light-gray;
	}

	&.is-hidden{
		transform: scaleX(0);
	}
}

.shares-btns{
	position: absolute;
	top: 0;
	transform: scaleX(0);
	transform-origin: left;
	transition: 0.2s ease;

	&.is-visible{
		transform: scaleX(1);
	}
}
/*--------------------END: Button animation--------------------*/