.field-box{
	padding: 0 15px;
	border-top: 3px solid $gray;
	border-bottom: 3px solid  $gray;
	
	@media(min-width: $sm){
		border-width: 6px;
		max-width: 700px;
		padding: 0 20px;
	}
	
	@media(min-width: $md){
		width: 760px;
		max-width: inherit;
		padding: 0 30px;
	}
	
	.textarea-wrapper{
		width: 100%;
		position: relative;
		
		@media(min-width: $sm){
			width: 95%;
		}
		
		@media(min-width: $md){
			width: 90%;
		}
		
		.reset-field{
			display: inline-block;
			position: absolute;
			right: 0px;
			top: 35px;
			z-index: 1;
			
			&__img{
				height: 40px;
				
				@media(min-width: $sm){
					height: auto;
				}
			}
		}
	}
	
	.textarea{
		height: 225px;
		width: 85%;
    	position: relative;
		display: inline-block;
		padding-top: 19px;
		overflow: hidden;
		
		@media(min-width: $sm){
			height: 325px;
		}
		
		&__label{
			position: absolute;
			top: 35px;
			cursor: text;
		}
		
		&__field{
			height: 100%;
			width: 100%;
			overflow: auto;
		}
	}
}